import React, { useState, useEffect } from "react";
import FileUploader from "./utils/FileUploader";
import { handleDownload } from "./utils/FileDownload";
import "./App.css";
import "./styles/base.css";

function App() {

  const linecount = 10;

  const [fileContent, setFileContent] = useState("");
  const [fileName, setFileName] = useState("custom_log.html");
  const [charColors, setCharColors] = useState({});
  const [charHeads, setCharHeads] = useState({});
  const [titleImages, setTitleImages] = useState([]);
  const [endImages, setEndImages] = useState([]);
  const [darkMode, setDarkMode] = useState(true); 
  const [inputTexts, setInputTexts] = useState([]);
  const [selectedCategories, setSelectedCategories] = useState({ main: true, info: false, other: false });

  const onDownloadClick = (type) => {
    handleDownload(() => parseHtml(false), fileName, type, darkMode);
  };

  const handleColorChange = (charName, color) => {
    setCharColors((prev) => ({ ...prev, [charName]: color }));
  };
  
  const handleCategoryChange = (category) => {
    setSelectedCategories((prev) => ({ ...prev, [category]: !prev[category] }));
  };
  
  const handleHeadChange = (charName, url) => {
    setCharHeads((prev) => ({ ...prev, [charName]: url }));
  };

  const handleTitleImageChange = (event) => {
    const inputText = event.target.value;
    const urls = inputText.split(",").map(url => url.trim()).filter(url => url);
    setTitleImages(urls);
  };


  const handleEndImageChange = (event) => {
    const inputText = event.target.value;
    const urls = inputText.split(",").map(url => url.trim()).filter(url => url);
    setEndImages(urls);
  };

  const DescChange = (event) => {
    const inputText = event.target.value;
    setInputTexts(inputText.split(",").map(text => text.trim()));
  };

  const createImageSection = (images) => {
    return `
      <div style="text-align: center; margin-top: 30px;">
        ${images
          .map(
            (url) => `
            <img src="${url}" style="max-width: 100%; height: auto; border-radius: 5px;">
            <hr style="border: 0; flex-shrink: 0; border-top: 1px solid rgba(255, 255, 255, 0.08);">
            `
          )
          .join("")}
      </div>
    `;
  };

  const titleImagesHtml = createImageSection(titleImages);
  const endImagesHtml = createImageSection(endImages);
  
  const parseHtml = (limitLines = true) => {
    if (!fileContent) return "파일을 업로드하세요.";
    const parser = new DOMParser();
    const doc = parser.parseFromString(fileContent, "text/html");
    const parsedDivs = [];
    let count = { main: 0, info: 0, other: 0 };
    let lastCharName = null;
    let lastCategory = null;

    parsedDivs.push(titleImagesHtml);

    Array.from(doc.querySelectorAll("p, div")).forEach((p) => {
      const spans = p.getElementsByTagName("span");
      if (spans.length >= 2) {
        const category = spans[0].textContent.trim().replace(/\[|\]/g, "");
        const charName = spans[1].textContent.trim();
        spans[0].textContent = "";
        const imgUrl = charHeads[charName] || "";
        let imgTag = "";
        let backgroundColor = "";
        let displayType = "flex";
    
        if (category === "other") {
          imgTag = "";
          backgroundColor = "#4c4c4c";
          p.style.paddingLeft = "55px";

        } else if (category === "info") {
          imgTag = `<div style="width: 40px; height: 40px; background: #4d4d4d; border-radius: 5px; display: flex; align-items: center; justify-content: center;">
                     <span style="color: #8d8d8d; font-size: 14px;"> 정보 </span>
                    </div>`;
          backgroundColor = "#464646";
          spans[1].innerHTML = "";
          const nextText = spans[1].nextSibling.textContent.trim();
          if (nextText === ":") {
            spans[1].nextSibling.textContent = "";
          }

        } else if (category === "main" && inputTexts.some(text => text === spans[1].textContent)) {
          const nextText = spans[1].nextSibling.textContent.trim();
            if (nextText === ":") {
              spans[1].nextSibling.textContent = "";
            }
            spans[1].innerHTML = "";
            displayType = "flow-root"
            p.style.display = "flow-root";
            p.style.fontStyle = "italic";
            p.style.fontWeight = "bold";
            p.style.textAlign = "center";

        } else {
          backgroundColor = "#3b3b3b";
          
          imgTag = imgUrl
            ? `<img src="${imgUrl}" alt="${charName}" style="width: 40px; height: 40px; object-fit: cover; border-radius: 5px;">`
            : `<img style="width: 40px; border-radius: 5px;">`;
        }
    
        p.style.color = category === "other" ? "gray" : category === "info" ? "#9d9d9d" : "#dddddd";
        if (category !== "other" && charColors[charName]) {
          spans[1].style.color = charColors[charName];
        }
    
        if (lastCharName === charName && lastCategory === category && category !== "info") {
          spans[1].innerHTML = "";
          const nextText = spans[1].nextSibling?.textContent.trim();
          if (nextText === ":") {
            spans[1].nextSibling.textContent = "";
            imgTag = "";
            p.style.paddingLeft = "95px";
          }
        } else {
          lastCharName = charName;
          lastCategory = category;
        }
    
        const messageHtml = `
        <div style="display: ${displayType}; align-items: center; gap: 15px; padding: 0 20px; background-color: ${backgroundColor};">
          ${imgTag ? `<div style="width: 40px; height: 40px; background: rgba(0, 0, 0, 0.2); border-radius: 5px; display: flex; align-items: center; justify-content: center;">${imgTag}</div>` : ""}
          ${p.outerHTML}
        </div>
        <hr style="margin: 0; padding: 0; border: 0; flex-shrink: 0; border-top: 1px solid rgba(255, 255, 255, 0.08);">
      `;
    
        if ((!limitLines && selectedCategories[category]) || (limitLines && selectedCategories[category] && count[category] < linecount)) {
          parsedDivs.push(messageHtml);
          count[category]++;
        }
      }
    });

    parsedDivs.push(endImagesHtml);
    

    return parsedDivs.length > 0 ? parsedDivs.join("") : "출력할 데이터가 없습니다.";;
  };

  useEffect(() => {
    
    document.body.style.backgroundColor = "rgba(44, 44, 44, 0.87)";
    if (fileContent) {
      const parser = new DOMParser();
      const doc = parser.parseFromString(fileContent, "text/html");
      const newCharColors = {};

      doc.querySelectorAll("p, div").forEach((el) => { // div도 포함
        const spans = el.getElementsByTagName("span");
        if (spans.length >= 2) {
          const charName = spans[1].textContent.trim();
          const rawColor = el.style.color || window.getComputedStyle(el).color;
          const convertedColor = rgbToHex(rawColor);
          newCharColors[charName] = convertedColor;
        }
      });
       setCharColors(newCharColors);
    }
  }, [fileContent]);

    const rgbToHex = (rgb) => {
      if (!rgb.startsWith("rgb")) return rgb;
      const rgbValues = rgb.match(/\d+/g);
      if (!rgbValues || rgbValues.length < 3) return "#FFFFFF";

      return `#${rgbValues
        .slice(0, 3)
        .map((val) => parseInt(val).toString(16).padStart(2, "0"))
        .join("")}`;
    };

  return (
    <div className="fix-layout">
      <div className="setting_container">
        <h2>CCFolia 채팅 로그 커스텀</h2>
        <p>
          *커스텀 하지 않은 코코포리아 로그 외에는 인식하지 않습니다 
          <a href="https://www.postype.com/@nell-dev/post/18656933">(사용법)</a>
        </p>
        <h4>01. 파일 업로드</h4>
        <FileUploader setFileContent={setFileContent} setFileName={setFileName}/>

        <div className="skinTypeCheck">
          <h4>02. 출력 탭 선택<b>(*중복 선택 가능)</b></h4>
          <ul>
              {Object.keys(selectedCategories).map((category) => (
                <li key={category}>
                  <input
                    type="checkbox"
                    id={category}
                    name="skinType"
                    value={category}
                    checked={selectedCategories[category]}
                    onChange={() => handleCategoryChange(category)}
                  />
                  <label htmlFor={category}>{category}</label>
                </li>
              ))}
          </ul>
        </div>

        <h4>03. 세션 제목 이미지 셋팅 <b>(*이미지 링크 사용. 쉼표 사용시 개행, 확장자까지 적어주세요.)</b></h4>
        <input type="text" placeholder="세션 제목 이미지 URL 입력란" className="title_input" 
          onChange={handleTitleImageChange} />

        <h4>04. 세션 엔딩 이미지 셋팅 <b>(*이미지 링크 사용. 쉼표 사용시 개행, 확장자까지 적어주세요.)</b></h4>
        <input type="text" placeholder="세션 엔딩 이미지 URL 입력란" className="end_input" 
          onChange={handleEndImageChange} />

        <h4>05. 시스템 스타일링 적용 캐릭터 이름 <b>(*입력한 캐릭터에게 적용 됩니다. 롤20 desc 같은)</b></h4>
        <input type="text" placeholder="스타일링 적용 캐릭터 이름 입력란" className="system_input" 
          onChange={DescChange}/>

        <h4>06. 캐릭터 네임태그 색 설정<b>(*마지막 선택 컬러 기준. 수정시 일괄 수정됩니다)</b></h4>
          <div className="color_div">
          {Object.keys(charColors).map((charName) => (
            <div key={charName} className="color_picker">
              <span>{charName} : {""}</span>
              <input
                type="color"
                className="color_link"
                value={charColors[charName] || "#000000"}
                onChange={(e) => handleColorChange(charName, e.target.value)}
              />
              <input
                  type="text"
                  placeholder="두상 URL"
                  value={charHeads[charName] || ""}
                  onChange={(e) => handleHeadChange(charName, e.target.value)}
                  style={{
                    
                  }}
                />
            </div>
          ))}
          </div>
      </div>

      {/* 오른쪽 미리보기 패널 */}
      <div className="preview_panel">
        <h3>미리보기</h3>
        <p>*각 탭의 {linecount}줄의 내용을 아래와 같이 출력합니다.</p>
        <div>
          <div dangerouslySetInnerHTML={{ __html: parseHtml(fileContent, charColors, charHeads, titleImages, selectedCategories) }} />
        </div>

        <div className="download_pannel">
        {fileContent && (
          <>
            <button onClick={() => onDownloadClick("html")} className="down_btn">
              다운로드(HTML)
            </button>
            <button 
            onClick={() => {
              alert("PDF 기능은 개발중입니다");
              // onDownloadClick("pdf");
            }}
             className="down_btn">
              다운로드(PDF)
            </button>
          </>
        )}

        </div>

      </div>
    </div>
  );
}

export default App;
